<template>
<div class="login-app">
    <img src="https://s2.ax1x.com/2019/03/21/A1863D.jpg" class="background-iamge" alt="">
   <div class="login-box">
       <div class="login-form">
           <h3>登录</h3><hr>
           <el-input class="myinput" v-model="uname" placeholder="请输入内容" clearable></el-input>
           <el-input type="password" class="myinput" v-model="upwd" placeholder="请输入密码" show-password></el-input>
           <el-checkbox class="autoLogin" v-model="autoLogin">自动登录</el-checkbox> 
            <a class="nopwd">忘记密码?</a>
           <el-button class="loginbtn" type="primary" @click="login"> 登录</el-button>
       </div>
   </div>
   <div class="ph"></div>
</div>
    
</template>

<script>
const unameReg = /^\w{8,12}$/
export default {
    data(){
        return{
            uname:"",
            upwd:"",
            autoLogin:false,
        }
    },
    methods:{
        login:function(){
            this.uname != "" && this.upwd != "" && this.$router.push("/")
        }
    }
}
</script>

<style lang="scss" scoped="" type="text/css">
.ph{
    padding-bottom:  300px;
}
.background-iamge{
    width: 100%;
    height: auto;
    position: absolute;
    z-index: 0;
    top: 0;left: 0;
}
.login-box{
    width: 100%;
    height: 650px;
    position: relative;
    .login-form{
    background: rgba(0, 0, 0, 0.5);
    width: 280px;
    height: 296px;
    position: absolute;
    top: 50%;
    left: 70%;
    margin-top: -148px;
    color: #fff;
        .loginbtn{
            width: 90%;
            position: absolute;
            bottom: 20px;
            left: 15px;
        }
        .autoLogin{ 
            margin-top: 15px;
            position: relative;            
            color: #fff;
            left: -33px;
        }
        .nopwd{
            padding: 5px;
            display: inline;
            font-size: 12px;
            border-radius:10px;
        }
        .myinput{
            width: 90%;
            margin-top: 20px;
        }
        h3{
            text-align: left;
            color: #fff;
            margin-left: 20px;
        }
        hr{
            width: 85%;
        }
    }
}

</style>



