<template>
    <div class="head-app"> 
          <el-menu
            default-active="1"
            class="el-menu-demo myNav"
            mode="horizontal"
            @select="handleSelect"
            background-color="#545c64"
            text-color="#fff"
            active-text-color="#ffd04b">
            <el-menu-item index="1" @click="jump('')">首页</el-menu-item>
            <el-submenu index="2">
              <template slot="title">电视剧</template>
              <el-menu-item :index="'2-'+k+1" v-for="(i,k) in navList[0]" :key="k">{{navList[0][k]}}</el-menu-item>
            </el-submenu>
            <el-submenu index="3">
              <template slot="title">电视剧</template>
              <el-menu-item :index="'3-'+k+1" v-for="(i,k) in navList[0]" :key="k">{{navList[1][k]}}</el-menu-item>
            </el-submenu>
            <el-submenu index="4">
              <template slot="title">电视剧</template>
              <el-menu-item :index="'4-'+k+1" v-for="(i,k) in navList[0]" :key="k">{{navList[2][k]}}</el-menu-item>
            </el-submenu>
            <el-submenu index="5">
              <template slot="title">电视剧</template>
              <el-menu-item :index="'5-'+k+1" v-for="(i,k) in navList[0]" :key="k">{{navList[3][k]}}</el-menu-item>
            </el-submenu>
            
            <a href="javascript:;" 
            class="myicon" 
            v-for="(i,k) in iconList" 
            :key="k" 
            @click="jump(i.link)">
              <i :class="i.class"></i>
              <p class="iname">{{i.text}}</p>
            </a>
          </el-menu>    
    </div>
</template>

<script>
  export default {
    data() {
      return {
        navList:[
          ["都挺好","知否","国家宝藏","都挺好",],
          ["复联4","绿皮书","白蛇-缘起","惊奇队长"],
          ["一拳超人2","进击的巨人3","辉夜姬大人","狐妖小红娘"],
          ["王牌对王牌4","最强大脑2","我和我的经纪人","我家那小子"],
        ],
        iconList:[
          {class:"el-icon-star-off",text:"收藏",link:"login"},
          {class:"el-icon-time"    ,text:"历史",link:"login"},
          {class:"el-icon-more"    ,text:"登录",link:"login"}]
        };
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key, keyPath);
      },
      jump(link){
        this.$router.push("/"+link)
      }
    }
  }
</script>

<style>
.iname{
  font-size: 12px;
  padding: 0;margin: 0;
}
.myicon{
  color: #fff;
  font-size: 24px;
  width: 30px;height: 30px;
  margin: 2px 10px;padding: 3px 10px 23px;
  float: right;
}
.head-app{
  position: fixed;
  top:0;
  z-index: 5;
  left: 50%;
  margin-left: -765px;
}
.myNav{
  margin: 0 auto ;
  width:80%;
  top: 0;
  position: relative;
  left: 50px;
}
.el-menu.el-menu--horizontal{
  border: 1px solid rgb(84, 92, 100)
}
#user-item{float: left;}
</style>



