<template>
    <div class="app-foot">
        <div class="foot-download">
            <ul class="download-list">
                <h2>服务</h2>
                <li class="download-item"  v-for="i in hpList" :key="i">
                    <a href="javascript:;">{{i}}</a>
                </li>
            </ul>
        </div>
        <div class="foot-download2">
            <ul class="download-list">
                <h2>多平台支持</h2>
                <li class="download-item"  v-for="(i,k) in downloadList" :key="k">
                    <a href="javascript:;">
                        <span class="icon-box iconfont" :class="i.icon"></span><br>
                        {{i.text}}
                    </a>
                </li>
            </ul>
        </div>
        <div class="foot-follow">
            <div class="follow-list">
                <img src="https://s2.ax1x.com/2019/03/21/A18DN6.jpg" alt="">
            </div>
        </div>

        <div class="bottom">
            <a class="bottom-item" href="javascript:;" v-for="i in bottomList" :key="i">{{i}}</a>
            <p class="bottom-text">Copyright © 1998 - 2019 Tencent. All Rights Reserved.</p>
            <p class="bottom-text">腾讯公司 版权所有 腾讯网络文化经营许可证</p>
        </div>        
    </div>
</template>
<script>
export default {
    name:"Foot",
    data(){
        return{
        bottomList:["关于腾讯","About Tencent","服务条款","广告服务","腾讯招聘","客服中心","网站导航"],
        hpList:["免广告合作","侵权投诉","VIP合作","客服","反馈"],
        downloadList:[{icon:"icon-shouji",text:"手机版"},
            {icon:"icon-win",text:"Windows版"},
            {icon:"icon-mac",text:"Mac版"},
            {icon:"icon-iPad",text:"iPad版"},
            {icon:"icon-tv",text:"TV版"}],
        }
    }
}
</script>

<style lang="scss" scoped="" type="text/css">
.app-foot{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 300px;
    background: #000;
    color: #fff;
    ul{
        list-style: none;
    }
    }
    .foot-follow{
        float: right;
        width: 230px;
        height: 230px;
        margin-right: 30px;
        margin-top: 20px;
        overflow: hidden;        
        img{
            width: 100%;
            height: auto;        
        }
    }
    .foot-download2{
        float: left;
        width: 600px;
        height: 176px;
        margin-left: -300px;
        position: absolute;
        left: 50%;
        top: 20px;
        .download-list{
            padding: 0;
            h2{
                margin: 8px;                
            }
            .download-item{
                margin:0 10px;
                font-size: 12px;    
                float: left;
                width: 100px;
                a{
                    padding: 8px 0;
                }
                .icon-box{
                    font-size: 70px;
                }
            }
        }    
    }
    .foot-download{
        float: left;
        width: 130px;
        height: 176px;
        margin-left: 50px;
        margin-top: 20px;
        .download-list{
        padding: 0;
        h2{
            margin: 8px;
        }
        .download-item{
            margin-top: 2px;
            font-size: 14px;    
        }
    }    
}

    .bottom{
        /* background: #f10; */
        height: 90px;
        position: absolute;
        width: 620px;
        bottom: 0;
        left: 50%;
        margin-left: -310px;
    }
    .bottom-item{
        display: inline;
        margin-right: 10px;
        font-size: 16px;
        padding: 2px;
    }

    .bottom-text{
        color: #fff;
        font-size: 12px;
    }

</style>



